<template>
    <div id="shop-item">
        <div class="item-selector">
             <!-- @checkBtnClick="checkedChange"  -->
            <check-button :value="itemInfo.checked"
            @click.native="checkClick" :is-checked="itemInfo.checked"></check-button>
        </div>
        <div class="item-img">
            <img :src="itemInfo.image" alt="商品图片">
        </div>
        <div class="item-info">
            <div class="item-title">{{itemInfo.title}}</div>
            <div class="item-desc">{{itemInfo.desc}}</div>
            <div class="info-bottom">
                <div class="item-price left">￥{{itemInfo.price}}</div>
                <div class="item-count right">{{itemInfo.count}}</div>
            </div>
        </div>
    </div>
</template>

<script>
import CheckButton from 'components/content/checkButton/CheckButton'
export default {
    props:{
        itemInfo:{
            type:Object,
            default(){
                return {}
            }
        }
    },
    components:{
        CheckButton
    },
    methods:{
        checkClick(){
            // console.log(this.itemInfo.checked)
            // this.itemInfo.checked = !this.itemInfo.checked
            this.$store.commit('change',this.itemInfo)
            // console.log(this.itemInfo);
        }
    }
}
</script>

<style>
#shop-item{
    width: 100%;
    display: flex;
    font-size: 0;
    padding: 5px;
    border-bottom: 1px solid #ccc;
}
.item-selector{
    width: 14%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.item-title, .item-desc {
    overflow: hidden;
    /* 指定元素内的空白怎样处理。
    文本不会换行，文本会在在同一行上继续，直到遇到 <br> 标签为止。 */
    white-space: nowrap;
    /* 指定当文本溢出包含它的元素，应该发生什么。
    显示省略符号来代表被修剪的文本。 */
    text-overflow: ellipsis;
  }
  .item-img {
    padding: 5px;
    /*border: 1px solid #ccc;*/
  }

  .item-img img {
    width: 80px;
    height: 100px;
    display: block;
    border-radius: 5px;
  }
  
  .item-info {
    font-size: 17px;
    color: #333;
    padding: 5px 10px;
    position: relative;
    overflow: hidden;
    /* width: 60%; */
  }

  .item-info .item-desc {
    font-size: 14px;
    color: #666;
    margin-top: 15px;
  }

  .info-bottom {
    margin-top: 10px;
    position: absolute;
    bottom: 10px;
    left: 10px;
    right: 10px;
  }

  .info-bottom .item-price {
    color: orangered;
  }
  .item-count{
      position:absolute;
      right: 0px;
      width: 30px;
  }
  .item-count ::before{
      content: "";

  }
</style>