<template>
  <div class="bottom-bar">
    <div class="check-content">
      <check-button
        class="check-button"
        :is-checked="isSelectAll"
        @click.native="checkClick"
      ></check-button>
      <span>全选</span>
    </div>

    <div class="total">合计:{{ totalPrice }}</div>

    <div class="calculate" @click="calcClick">去计算:({{ checkLength }})</div>
  </div>
</template>

<script>
import CheckButton from "components/content/checkButton/CheckButton";

import { mapGetters } from "vuex";
export default {
  components: {
    CheckButton
  },
  computed: {
    ...mapGetters(["cartList"]),
    totalPrice() {
      return (
        "￥" +
        this.$store.state.cartList
          .filter(item => {
            // console.log("---");
            return item.checked;
          })
          .reduce((preValue, item) => {
            return preValue + item.count * item.price;
          }, 0)
          .toFixed()
      );
    },
    checkLength() {
      return this.cartList.filter(item => item.checked).length;
    },
    isSelectAll() {
        if(this.cartList.length === 0)
            return false
        else{
            // console.log("-------------------");
            // return !(this.cartList.filter(item=> !item.checked).length)

            return this.cartList.every((item) => item.checked);

           //return !this.cartList.find((item) => !item.checked)
        }
      
    },
  },
  methods:{
      checkClick(){
          this.$store.commit('isSelect',this)
      },
       calcClick(){
        if(!this.checkLength){
          this.$toast.show("请添加要购买的商品",2000)
        }
      }
      
  }
};
</script>

<style scoped>
.bottom-bar {
  height: 40px;
  line-height: 40px;
  background-color: #eee;
  /* position: relative; */
  display: flex;
}
.check-content {
  width: 80px;
  display: flex;
  align-items: center;
  font-size: 15px;
}
.check-button {
  width: 20px;
  height: 20px;
  line-height: 20px;
  margin: 0 6px;
}
.total {
  /* 让弹性盒模型对象的子元素都有相同的长度，且忽略它们内部的内容： */
  flex: 1;
  margin-left: 10px;
}
.calculate {
  width: 100px;
  background-color: rgba(255, 139, 220, 0.719);
  color: #fff;
  font-size: 16px;
  padding-left: 10px;
}
</style>
